@tailwind base;
@tailwind components;
@tailwind utilities;

.range{
    color: #eab308;
}
.flex-none{
    flex: none;
}
.h-500{
    height: 500px;
}
.line-through{
    text-decoration: line-through;
}
#category-header img{
    border: solid 4px #ffffff;
    transition: 1s;
}
#category-header img:hover{
    border: solid 4px #571414;
    transition: 1s;
}
#category-header .active{
    border: solid 4px #571414;
}

.product-icon{
    background-color: #571414;
    margin-left: 15px;
    cursor: pointer;
}
.product-icon:hover{
    background-color: #232323;

}
.item-products .product-icon{
    opacity: 0;
}
.item-products:hover .product-icon:nth-child(1){
    opacity: 1;
    transition: .5s;
    transition-delay: .1s;
    transform: rotateX(360deg);
}
.item-products:hover .product-icon:nth-child(2){
    opacity: 1;
    transition: .5s;
    transition-delay: .2s;
    transform: rotateX(360deg);
}
.item-products:hover .product-icon:nth-child(3){
    opacity: 1;
    transition: .5s;
    transition-delay: .3s;
    transform: rotateX(360deg);
}